import React, { Component } from 'react';

import { robots } from '../robots'
import CardList from '../components/CardList'
import SearchField from '../components/SearchField'
import Scroll from '../components/Scroll'

class App extends Component {
    constructor() {
        super()
        this.state = {
            robots: [],
            searchText: "",
            searchType: ""
        }
    }

    componentDidMount() {
        // fetch('https://pokeapi.co/api/v2/pokemon/').then(response => {
        //     return response.json()
        // }).then(users => {
        //     console.log(users)
        //     this.setState({robots: users})
        // })
        this.setState({robots: robots})
        console.log("mounted")
    }

    onSearchChange = (event) => {
        this.setState({searchText: event.target.value})
    }

    onTypeChange = (event) => {
        this.setState({searchType: event.target.value})
    }

    render() {

        const filterdRobots = this.state.robots.filter(robot => {
            return robot.name.toLocaleLowerCase().includes(this.state.searchText.toLocaleLowerCase()) && 
            robot.type.toLocaleLowerCase().includes(this.state.searchType.toLocaleLowerCase())
        })

        return(
            <div className="tc">
                <h1 className="f1 yellow">Pokedex</h1>
                <SearchField plc="Search Pokemon" searchChange={this.onSearchChange}/>
                <SearchField plc="Search Type" searchChange={this.onTypeChange}/>
                <Scroll>    
                    <CardList robots={filterdRobots}/>
                </Scroll>
            </div>
        );
    }
    
};

export default App